import "./index.css";
import React, { useState, useEffect } from "react";
import { getFullMessages, createMessage } from "../../services/messages";
import { getUser } from "../../services/users";
import { useNavigate } from "react-router-dom";

import TypingStatusCard from "./TypingStatusCard";
import MessageInput from "./MessageInput";
import MessagesUI from "./MessagesUI";
import ImageUpload from "./ImageUpload";
import { autoScroll, forceScroll, instantScroll } from "./ScrollFunctions";
import { getToken } from "../../services/messages";

var scrollStatement = "instant";
var isTyping = false;
var messages_style = { left: "0%" };

const MessagesBox = ({ socket, user, setUser, setErrorMessage }) => {
  const [messages, setMessages] = useState([]);
  const [messageToSubmit, setMessageToSubmit] = useState("");
  const [countUnread, setCountUnread] = useState(0);
  const [usersTyping, setUsersTyping] = useState([]);
  const [imageUrl, setImageUrl] = useState(null);
  const [openUserInfo, setOpenUserInfo] = useState(false);
  const navigate = useNavigate();

  socket.on("message", (data) => {
    scrollStatement = "auto";
    setMessages([...messages, data]);
  });

  socket.on("typing", (data) => {
    if (data.type === "typing") {
      setUsersTyping(usersTyping.concat(data.name));
    } else if (data.type === "stop") {
      const newUsersTyping = usersTyping.filter((user) => user !== data.name);
      setUsersTyping(newUsersTyping);
    }
  });

  useEffect(() => {
    getFullMessages()
      .then((res) => {
        setMessages(res);
      })
      .catch(() =>
        setErrorMessage(">>> It looks like we can't get messages from database")
      );
  }, [imageUrl]);

  useEffect(() => {
    var id;
    if (user) {
      id = user.id;
    } else {
      const loggedUserJSON = window.localStorage.getItem("LoggedChatappUser");
      const curUser = JSON.parse(loggedUserJSON);
      setUser(curUser);
      getToken(curUser.id);
      id = curUser.id;
    }

    getUser(id)
      .then((res) => {
        setImageUrl(res.imageUrl);
      })
      .catch(() => {
        setErrorMessage("It looks like we can not get user. Login is required");
        window.localStorage.removeItem("LoggedChatappUser");
        navigate("/login");
      });
  }, [imageUrl]);

  useEffect(() => {
    if (scrollStatement === "auto") autoScroll(countUnread, setCountUnread);
    else if (scrollStatement === "force") forceScroll(setCountUnread);
    else if (scrollStatement === "instant") instantScroll(setCountUnread);
  }, [messages]);

  const handleTyping = (value) => {
    if (value !== "" && !isTyping) {
      isTyping = true;
      socket.emit("typing", { type: "typing", name: user.name });
    } else if (value === "" && isTyping) {
      isTyping = false;
      socket.emit("typing", { type: "stop", name: user.name });
    }
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    const newMessage = {
      content: messageToSubmit,
      time: new Date().toLocaleTimeString([], {
        year: "numeric",
        month: "numeric",
        day: "numeric",
        hour: "2-digit",
        minute: "2-digit",
        hour12: false,
      }),
    };

    createMessage(newMessage)
      .then((res) => {
        scrollStatement = "force";
        setMessages(messages.concat(res));
        socket.emit("message", res);
        socket.emit("typing", { type: "stop", name: user.name });
      })
      .catch((err) => {
        setErrorMessage(err.response.data.error);

        if (err.response.data.name === "loginRequired") {
          window.localStorage.removeItem("LoggedChatappUser");
          navigate("/login");
        }
      });

    setMessageToSubmit("");
  };

  const handleUserToggle = () => {
    console.log(openUserInfo);
    if (openUserInfo) {
      messages_style = { left: "0%" };
    } else {
      messages_style = { left: "20%" };
    }
    setOpenUserInfo(!openUserInfo);
  };
  return (
    <div>
      <ImageUpload
        user={user}
        imageUrl={imageUrl}
        setImageUrl={setImageUrl}
        setOpenUserInfo={setOpenUserInfo}
        setErrorMessage={setErrorMessage}
      />
      <div style={messages_style} className="messages_container">
        <MessagesUI messages={messages} />
        <TypingStatusCard usersTyping={usersTyping} />
        <MessageInput
          user={user}
          imageUrl={imageUrl}
          countUnread={countUnread}
          handleSubmit={handleSubmit}
          handleTyping={handleTyping}
          handleUserToggle={handleUserToggle}
          messageToSubmit={messageToSubmit}
          setMessageToSubmit={setMessageToSubmit}
        />
      </div>
    </div>
  );
};

export default MessagesBox;
